import { throttle } from "lodash";
import { Store } from "vuex";
// @ts-ignore
import SharedWorker from "./shared.worker.js";
const HOST = 0;
const CLIENT = 1;
let _worker: any = null;

export const registerWorker = (store: Store<any>, role = HOST) => {
	if (!_worker) {
		// @ts-ignore
		_worker = new SharedWorker();
		_worker.port.start();
	}
	const worker = _worker;
	worker.port.onmessage = (e: any) => {
		const [method, data] = e.data;
		// console.log(data);
		switch (method) {
			case "dispatch":
			case "commit":
				if (role === HOST) {
					const [type, payload] = JSON.parse(data);
					// @ts-ignore
					store[method](type, payload);
				}
				break;
			case "stateUpdate":
				if (role === CLIENT) {
					const state = JSON.parse(data);
					store.replaceState(state);
				}
		}
	};
	worker.port.postMessage(["register", role]);
	window.addEventListener("beforeunload", () => {
		worker.port.postMessage(["close"]);
	});
	const { commit, dispatch } = store;

	const fn = throttle(function() {
		if (role === HOST) {
			worker.port.postMessage([
				"stateUpdate",
				JSON.stringify(store.state)
			]);
		}
	}, 1000);

	store.watch(
		state => {
			return state;
		},
		fn,
		{
			deep: true
		}
	);

	Object.defineProperties(store, {
		dispatch: {
			value: new Proxy(dispatch, {
				apply: function(fn, store, [type, payload]) {
					if (role !== HOST) {
						worker.port.postMessage([
							"dispatch",
							JSON.stringify([type, payload])
						]);
						return;
					}
					return dispatch.apply(store, [type, payload]);
				}
			}),
			configurable: true
		},
		commit: {
			value: new Proxy(commit, {
				apply: function(fn, store, [type, payload]) {
					if (role !== HOST) {
						worker.port.postMessage([
							"commit",
							JSON.stringify([type, payload])
						]);
						return;
					}
					return commit.apply(store, [type, payload]);
				}
			}),
			configurable: true
		}
	});
};

export const unRegisterWorker = () => {
	if (!_worker) {
		return;
	}
	_worker.port.postMessage(["close"]);
	_worker.port.close();
	_worker = null;
};
