<template>
	<div class="outer">
		<row-frame class="rowframe" title="播放器" contentClass="list">
			<title-scrolling style="width:100%;white-space:nowrap" :label="`正在播放：${songInfo.title||'暂无播放歌曲'} ${songInfo.author?` - ${songInfo.author}`:''}`" />
			<div class="title"></div>
			<div class="player">
				<audio autoplay controls :src="playingSingle.url" />
			</div>
		</row-frame>
		<row-frame class=" rowframe" title="歌词" contentClass="list">
			<div class="lyric">{{songInfo.lyric||"暂无歌词"}}</div>
		</row-frame>
		<row-frame class="rowframe playerList" title="待播列表" contentClass="list">
			<div class="song-row active" v-if="playing">
				<div class="song-name">
					<title-scrolling :label="playing.title" />
					<title-scrolling :label="playing.nickName" />
				</div>
				<el-button-group>
					<el-button class="btn" size="mini" @click="playing = null;startSong()">移除</el-button>
				</el-button-group>
			</div>
			<div class="song-row" v-for="song in pending" :key="song.title">
				<div class="song-name">
					<title-scrolling :label="song.title" />
					<title-scrolling :label="song.nickName" />
				</div>
				<el-button-group>
					<el-button class="btn" size="mini" @click="switchTo(song)">切歌</el-button>
					<el-button class="btn" size="mini" @click="pending = pending.filter(i=>i!==song)">移除</el-button>
				</el-button-group>
			</div>
		</row-frame>
		<div class="row">
			<el-select style="width:100px" size="mini" v-model="settings.addType">
				<el-option label="编号" value="id" />
				<el-option label="歌名" value="name" />
			</el-select>
			<el-input size="mini" v-model="search" placeholder="输入歌名/编号，enter添加" @keydown.enter="addSongSearch" :disabled="loading" />
		</div>
		<el-dialog v-model="settingShow">
			<div class="row">
				<span class="shadow">冷却时间</span>
				<el-input-number size="mini" v-model="settings.coolingTime" :min="1" />
			</div>
			<div class="row">
				<span class="hint">单位为分钟，限制同一人频繁点歌</span>
			</div>
			<div class="row">
				<span class="shadow">点歌反馈</span>
				<el-switch size="mini" v-model="settings.feedback" :min="1" />
			</div>
			<div class="row">
				<span class="hint">会以弹幕回复观众点歌是否成功，如不成功不会进入冷却时间或消耗点歌券</span>
			</div>
			<div class="row">
				<span class="shadow">需要送礼购买点歌券</span>
				<el-switch size="mini" v-model="settings.needGift" :min="1" />
			</div>
			<div class="row" v-if="settings.needGift">
				<span class="shadow">礼物类型</span>
				<el-select size="mini" v-model="settings.giftName" max-height="100px">
					<el-option v-for="gift in gifts" :key="gift.giftID" :value="gift.giftName" :label="gift.giftName" />
				</el-select>
			</div>
			<div class="row" v-if="settings.needGift">
				<span class="hint">送出指定礼物后再输入"#点歌 歌曲名#"进行点歌</span>
			</div>
			<div class="row">
				<span class="shadow">歌名大小</span>
				<el-input-number @change="wsUpdate" size="mini" v-model="settings.titleSize" :min="1" />
			</div>
			<div class="row">
				<span class="shadow">歌名颜色</span>
				<el-color-picker @change="wsUpdate" size="mini" v-model="settings.titleColor" />
			</div>
			<div class="row">
				<span class="shadow">歌名轮廓颜色</span>
				<el-color-picker @change="wsUpdate" size="mini" v-model="settings.titleShadow" />
			</div>
			<div class="row">
				<span class="shadow">歌词大小</span>
				<el-input-number @change="wsUpdate" size="mini" v-model="settings.lyricSize" :min="1" />
			</div>
			<div class="row">
				<span class="shadow">歌词颜色</span>
				<el-color-picker @change="wsUpdate" size="mini" v-model="settings.lyricColor" />
			</div>
			<div class="row">
				<span class="shadow">歌词轮廓颜色</span>
				<el-color-picker @change="wsUpdate" size="mini" v-model="settings.lyricShadow" />
			</div>
			<div class="row">
				<el-input size="mini" :model-value="url">
					<template #append>
						<el-button type="primary" size="mini" class="btnBase attach" @click="copy(url)">复制</el-button>
					</template>
				</el-input>
			</div>

			<span class="hint">推荐尺寸：400px x 100px</span>
		</el-dialog>
	</div>
</template>

<script>
/* eslint-disable */
import { defineComponent } from "vue";
import { mapState } from "vuex";
import {
	registerWorker,
	unRegisterWorker
} from "@front/util_function/storeWorker";
import {
	getContent,
	getUID,
	getTime,
	getNickName,
	getGiftName
} from "@front/components/danmakuFlow/utils/getter";
import {
	hasContent,
	hasGift,
	isOwner
} from "@front/components/danmakuFlow/utils/tester";
import { wsevent } from "@front/api/wsbus";
import { isOnline, isConnecting, init } from "@front/api/server";
import { user, room } from "@front/api";
import { copy } from "@front/util_function/clipboard";
import { chat } from "@front/api/chat";
import { ElMessage } from "element-plus";
export default defineComponent({
	name: "danmakuSongPicker",
	cname: "弹幕点歌台",
	icon: "self-song",
	description:
		'观众输入"点歌@歌曲名@"进行点歌,"编号点歌@网易云歌曲id@进行编号点歌"，会合并同名请求，右上角设置提供obs端显示歌曲名和歌词、冷却时间, 会与歌回助手冲突！！',
	configurations: {
		width: 300,
		height: 500,
		resizable: true,
		multiple: false,
		liveOnly: false
	},
	data() {
		return {
			sendedGift: new Set(),
			feedbackArray: [],
			pending: [],
			cooling: [],
			playing: null,
			playIndex: 0,
			feedbackTimer: null,
			search: "",
			loading: false,
			settings: {
				coolingTime: 1,
				addType: "name",
				feedback: false,
				needGift: false,
				giftName: "香蕉",
				lyricSize: 20,
				lyricColor: "rgba(255,255,255,1)",
				lyricShadow: "rgba(0,0,0,1)",
				titleSize: 16,
				titleColor: "rgba(255,255,255,1)",
				titleShadow: "rgba(0,0,0,1)"
			},
			lyricInfo: {
				title: "",
				lyrics: []
			},
			songInfo: {
				title: "",
				lyric: ""
			},
			playTime: 0,
			gifts: [],
			url: `http://${window.location.host}/obs/${this.$options.name}`,
			settingShow: false
		};
	},
	mounted() {
		registerWorker(this.$store, 1);
		this.$emit("hasSetting", this.showSetting);
		this.registerOBS();
		this.getGift();
		this.checkFeedback();
		this.loadSettings();
		this.addAudioListeners();
		window.onbeforeunload = this.saveSettings;
	},
	beforeUnmount() {
		unRegisterWorker(this.$store);
		clearTimeout(this.timer);
	},
	computed: {
		...mapState([
			"danmakuSession",
			"userProfile",
			"roomProfile",
			"userSession"
		]),
		playingSingle() {
			if (this.playing) {
				return (
					this.playing.songList[this.playIndex] || {
						url: "",
						lrc: ""
					}
				);
			}
			return { url: "", lrc: "" };
		}
	},
	watch: {
		"danmakuSession.filterFlow"(n, o) {
			if (o.length == 0) {
				return;
			}
			const output = [];
			const i = o[0];
			for (const danmaku of n) {
				if (getTime(danmaku) === getTime(i)) break;
				if (
					this.settings.needGift &&
					hasGift(danmaku) &&
					getGiftName(danmaku) === this.settings.giftName
				) {
					this.sendedGift.add(getUID(danmaku));
					continue;
				}
				if (hasContent(danmaku)) {
					output.push(danmaku);
				}
			}
			this.addSong(output);
		}
	},
	methods: {
		copy,
		generateLyric() {
			if (!this.playingSingle.url) return;
			try {
				const lrc = this.playingSingle.lrc;
				const lrcs = lrc.split("\n").filter(i => i.trim());
				const lrcList = lrcs.reverse();
				this.lyricInfo.lyrics = lrcList.map(lrca => {
					const splitted = lrca.split(/\[|\]/g).filter(i => i);
					return {
						time: splitted[0]
							.split(":")
							.reduce((acc, time) => 60 * acc + +time),
						text: splitted[1]
					};
				});

				this.lyricInfo.title = this.playingSingle.title;
			} catch (error) {
				console.log(this.playingSingle);
			}
		},
		addAudioListeners() {
			const audio = document.querySelector("audio");
			audio.ontimeupdate = e => {
				this.playTime = audio.currentTime;
				let lyric = "";
				let title = "";
				let singer = "";
				do {
					if (!this.playingSingle.url) break;
					if (this.lyricInfo.title !== this.playingSingle.title) {
						this.generateLyric();
					}
					lyric = this.lyricInfo.lyrics.find(
						ly => ly.time < this.playTime + 0.5
					)?.text;
					title = this.playingSingle.title;
					singer = this.playingSingle.author;
				} while (false);
				this.songInfo = { lyric, title, singer };
				this.wsUpdate();
			};
			audio.onerror = e => {
				if (!this.playing) {
					this.startSong();
					return;
				}
				if (this.playIndex < this.playing.songList.length - 1) {
					this.playIndex += 1;
					return;
				}
				this.playing = null;
				this.startSong();
			};
			audio.onended = e => {
				this.playing = null;
				this.startSong();
			};
		},
		async getGift() {
			let retry = Boolean(this.roomProfile.liveID);
			if (retry) {
				try {
					await user.login({
						account: "",
						password: ""
					});
					const res = await room.getGiftList({
						liveID: this.roomProfile.liveID
					});
					this.gifts = res;
				} catch (error) {
					retry = false;
					console.log(error);
				}
			}
			if (!retry) {
				setTimeout(() => {
					this.getGift();
				}, 1000);
			}
		},
		checkFeedback() {
			clearTimeout(this.feedbackTimer);
			const feedback = this.feedbackArray.shift();
			if (this.settings.feedback && feedback) {
				chat({
					userID: this.userSession.userID,
					deviceID: this.userSession.deviceID,
					serviceToken: this.userSession.serviceToken,
					data: {
						visitorId: this.userSession.userID,
						liveId: this.roomProfile.liveID,
						content: `用户 ${
							feedback.nickName
						}点歌 ${feedback.title.substr(0, 10)} ${
							feedback.type
						}:${feedback.message}`
					}
				});
			}
			this.feedbackTimer = setTimeout(() => {
				this.checkFeedback();
			}, 5000);
		},
		getSong(songName, type = "name") {
			return new Promise((resolve, reject) => {
				const formData = new FormData();
				formData.append("input", songName);
				formData.append("filter", type);
				formData.append("type", "netease");
				formData.append("page", "1");
				const xhr = new XMLHttpRequest();

				xhr.open("POST", "http://music.laomao.me/");
				xhr.setRequestHeader("X-Requested-With", "XMLHttpRequest");
				xhr.setRequestHeader(
					"Content-Type",
					"application/x-www-form-urlencoded; charset=UTF-8"
				);
				xhr.onloadend = () => {
					resolve(JSON.parse(xhr.responseText).data);
				};
				xhr.send(new URLSearchParams(formData).toString());
			});
		},
		addSongSearch() {
			const songName = this.search;
			if (!songName) return;
			const type = this.settings.addType;
			this.loading = true;
			this.getSong(songName, type)
				.then(songList => {
					if (!songList.length) {
						ElMessage({
							type: "error",
							message: "找不到歌曲",
							duration: 1500
						});
						return;
					}
					const newSong = {
						userID: "10086",
						content: "",
						nickName: "主播",
						title: type === "name" ? songName : songList[0].title,
						time: 1000,
						songList: songList.map(song => {
							if (!song.url) song.url = "123123123";
							return song;
						})
					};

					this.pending = [...this.pending, newSong];
					this.search = "";
				})
				.finally(() => {
					this.loading = false;
					this.startSong();
				});
		},
		registerOBS() {
			clearTimeout(this.timer);
			if (!isOnline() && !isConnecting()) {
				init({
					onOpen: () => {
						wsevent.register(`client-${this.$options.name}`);
						this.wsUpdate();
					}
				});
			}

			this.timer = setTimeout(() => {
				this.registerOBS();
			}, 3000);
		},
		wsUpdate() {
			wsevent.wsEmit(
				`lyric-update`,
				{
					songInfo: this.songInfo,
					settings: this.settings
				},
				`obs-lyric`
			);
		},
		addSong(danmakus) {
			const time = Date.now();
			this.cooling = this.cooling.filter(
				i => time - i.time < 1000 * 60 * this.settings.coolingTime
			);
			danmakus.forEach(danmaku => {
				const uid = getUID(danmaku);
				const nickName = getNickName(danmaku);
				const owner = isOwner(danmaku, this.$store.state);
				if (this.cooling.find(i => i.userID === uid)) {
					return;
				}
				if (
					!owner &&
					this.settings.needGift &&
					!this.sendedGift.has(uid)
				) {
					return;
				}

				const content = getContent(danmaku);
				if (!content || !content.includes("点歌")) return;
				let songName = content.split(/@/)[1];
				if (!songName) return;
				songName = songName.trim();
				let type = "name";
				if (content.includes("编号")) {
					type = "id";
				}
				this.getSong(songName, type).then(songList => {
					if (!songList.length && !owner) {
						this.feedbackArray.push({
							nickName,
							title: songName,
							userID: uid,
							type: "失败",
							message: "找不到该歌曲"
						});
						return;
					}
					const newSong = {
						userID: uid,
						content,
						nickName,
						title: type === "name" ? songName : songList[0].title,
						time,
						songList: songList.map(song => {
							if (!song.url) song.url = "123123123";
							return song;
						})
					};

					this.pending = [...this.pending, newSong];

					this.sendedGift.delete(uid);
					if (!owner) {
						this.cooling = [newSong, ...this.cooling];
						this.feedbackArray.push({
							nickName,
							title: songName,
							userID: uid,
							type: "成功",
							message: `已添加入队列,排号${this.pending.length}`
						});
					}

					this.startSong();
				});
			});
		},
		showSetting() {
			this.settingShow = true;
		},
		startSong() {
			if (this.playing) {
				return;
			}
			this.playIndex = 0;
			this.playing = this.pending.shift();
		},
		switchTo(song) {
			this.pending = this.pending.filter(i => i !== song);
			this.playIndex = 0;
			this.playing = song;
		},
		loadSettings() {
			let settings = localStorage.getItem(`applet$${this.$options.name}`);
			if (settings) {
				try {
					settings = JSON.parse(settings);
					Object.assign(this.settings, settings);
				} catch (error) {}
			}

			this.upgradeSettings();
		},
		upgradeSettings() {
			document.body.style.setProperty(
				"--opacity",
				this.settings.opacity / 100
			);
		},
		saveSettings() {
			localStorage.setItem(
				`applet$${this.$options.name}`,
				JSON.stringify(this.settings)
			);
		}
	}
});
</script>
<style scoped lang='scss'>
.center-column {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.center-column > * {
	margin-bottom: 8px;
}
@font-face {
	font-family: "站酷快乐体";
	src: url("/fonts/站酷快乐体.ttf") format("truetype");
}
.shadow {
	text-shadow: rgb(0, 0, 0) -0.8px -0.8px 0px, rgb(0, 0, 0) 0px -0.8px 0px,
		rgb(0, 0, 0) 0.8px -0.8px 0px, rgb(0, 0, 0) 0.8px 0px 0px,
		rgb(0, 0, 0) 0.8px 0.8px 0px, rgb(0, 0, 0) 0px 0.8px 0px,
		rgb(0, 0, 0) -0.8px 0.8px 0px, rgb(0, 0, 0) -0.8px 0px 0px;
	color: white;
	font-family: 站酷快乐体;
}
.song-row {
	box-sizing: border-box;
	display: flex;
	justify-content: space-between;
	width: 100%;
	margin-bottom: 8px;
	align-items: center;
	padding: 4px;
	.song-name {
		flex-grow: 1;
		flex-shrink: 1 !important;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		display: flex;

		& > div {
			margin-right: 8px;
			width: calc(50% - 8px);
		}
	}
	& > * {
		flex-shrink: 0;
	}
	.btn {
		padding: 0px 4px !important;
	}
}
.rowframe {
	box-sizing: border-box;
	width: 100% !important;
	padding: 4px;

	&.playerList {
		flex-grow: 1;
		max-height: calc(100vh - 250px);
	}
	&:deep .list {
		width: 100%;
		position: relative;
		padding-right: 4px;
		box-sizing: border-box;
		overflow-y: scroll;
		height: calc(100% - 24px);
		&:hover {
			padding-right: 0px;
		}
		&::-webkit-scrollbar {
			-webkit-appearance: none;
			width: 4px;
			border-radius: 5px;
			height: 10px;
			transition: background-color 1s;
			display: none;
		}
		&:hover {
			&::-webkit-scrollbar {
				display: block;
			}
		}

		&::-webkit-scrollbar-thumb {
			border-radius: 4px;
			background-color: rgba(0, 0, 0, 0.3);
		}

		&::-webkit-scrollbar-thumb:hover {
			background-color: rgba(0, 0, 0, 0.4);
		}

		&::-webkit-scrollbar-thumb:window-inactive {
			background-color: rgba(0, 0, 0, 0.3);
		}
	}
}
.outer {
	position: absolute;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	background-size: cover;
	background-position: center;
	background-color: whitesmoke;
	border: 2px solid grey;
	display: flex;
	flex-direction: column;
	.left {
		width: calc(100% - 250px);
	}
	.right {
		width: 250px;
		height: 100%;
		position: relative;
	}
}

.player {
	width: 100%;
	text-align: center;
	& > * {
		width: 100%;
		outline: none !important;
	}
}
.active {
	background-color: rgba(102, 167, 218, 0.5);
}
.lyric {
	box-sizing: border-box;
	width: 100%;
	padding: 4px 8px;
	word-break: break-all;
	text-align: center;
}

.row {
	box-sizing: border-box;
	justify-content: space-between;
	display: flex;
	align-items: center;
	padding: 0px 16px 8px 16px;
	word-break: break-all;
}
.hint {
	flex-shrink: 1;
	font-size: 14px !important;
	word-break: break-all;
}
span {
	flex-shrink: 0;
	font-size: 16px;
	word-break: break-all;
}
</style>
