
import { defineComponent } from "vue";
import { getMedal } from "../../utils/getter";
import {
	padding,
	margin,
	border,
	outerBox,
	font,
	position,
	transform
} from "../../utils/styleGetter";
import defaultValue from "./default";
import form from "./form";
export default defineComponent({
	name: "medal",
	cname: "牌子",
	widgetOptions: {
		avaliable: [1000, 1001, 1002, 1003, 1005],
		limit: -1
	},
	props: {
		danmaku: {
			required: true
		},
		setting: {
			default: defaultValue()
		}
	},
	data() {
		return {
			settingValue: defaultValue,
			settingForm: form
		};
	},
	computed: {
		style(): any {
			// @ts-ignore
			const style = this.setting?.style;
			if (!style) {
				return {};
			}
			return {
				...padding(style),
				...margin(style),
				...border(style),
				...outerBox(style),
				...position(style),
				...transform(style)
			};
		},
		degreeStyle(): any {
			// @ts-ignore
			const style = this.setting?.degreeStyle;
			if (!style) {
				return {};
			}
			return {
				...padding(style),
				...margin(style),
				...border(style),
				...outerBox(style),
				...font(style),
				...position(style),
				...transform(style)
			};
		},
		nameStyle(): any {
			// @ts-ignore
			const style = this.setting?.nameStyle;
			if (!style) {
				return {};
			}
			return {
				...padding(style),
				...margin(style),
				...border(style),
				...outerBox(style),
				...font(style),
				...position(style),
				...transform(style)
			};
		}
	},
	methods: {
		getMedal(danmaku: any) {
			return getMedal(danmaku);
		}
	}
});
