
import { defineComponent } from "vue";
import widgets from "@front/components/danmakuFlow/widgets";
import { getUserInfo } from "@front/components/danmakuFlow/utils/getter";
import {
	padding,
	margin,
	border,
	outerBox,
	transform
} from "@front/components/danmakuFlow/utils/styleGetter";
export default defineComponent({
	name: "danmakuRow",
	props: {
		setting: {
			required: true
		},
		danmaku: {
			required: true
		},
		configMode: {
			default: false
		}
	},
	data() {
		return {
			widgets
		};
	},
	computed: {
		danmakuWidgets(): any {
			// @ts-ignore
			return this.setting.widgets.filter(
				(widget: any) => widget.labelEn !== "mainBlock"
			);
		},
		style(): any {
			try {
				// @ts-ignore
				const style = this.setting.widgets.find(
					(widget: any) => widget.labelEn === "mainBlock"
				)?.value.style;
				if (style) {
					return {
						...padding(style),
						...margin(style),
						...border(style),
						...outerBox(style),
						...transform(style)
					};
				}
				return {};
			} catch (error) {
				console.error(error);
				return {};
			}
		}
	},
	methods: { getUserInfo }
});
